html,
body {
  height: 100%;
  font-size: 16px;
  font-weight: 400;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
* {
  margin: 0;
  padding: 0;
}
html,
body {
  width: 100%;
  height: 100%;
  color: #3c3c3b;
}
.b-wrapper {
  width: 100%;
  display: table;
  height: 100%;
}
.content {
  width: 980px;
  height: 100%;
  margin: 0 auto;
}
.header {
  padding-top: 20px;
  padding-bottom: 20px;
}
.header-top {
  width: 100%;
}
.header-top__box {
  display: inline-block;
  margin-left: 100px;
}
.header-top__box:first-child {
  margin-left: 15px;
}
.header-top__logo {
  vertical-align: middle;
  display: inline-block;
  margin-left: 80px;
}
.header-top__logo-link {
  display: inline-block;
  vertical-align: bottom;
  padding-top: 10px;
}
.header__search {
  width: 237px;
  padding: 8px;
  padding-right: 60px;
  font-size: 12px;
  border: 1px solid #c8c8c8;
}
.header__search-link {
  display: inline-block;
  width: 31px;
  height: 33px;
  margin-left: 10px;
  background: #fff url("../img/spritesheet.png") no-repeat -5px -100px;
  vertical-align: top;
}
.header__search-link_doc:hover {
  background: url("../img/spritesheet.png") no-repeat -46px -100px;
}
.header__search-link_doc:active {
  background: url("../img/spritesheet.png") no-repeat -46px -97px;
}
.header__search-link_theme {
  background: url("../img/spritesheet.png") no-repeat -26px -5px;
}
.header__search-link_theme:hover {
  background: url("../img/spritesheet.png") no-repeat -67px -5px;
}
.header__search-link_theme:active {
  background: url("../img/spritesheet.png") no-repeat -67px -2px;
}
.nav__list {
  max-width: 780px;
  margin: 0 auto;
  margin-top: 30px;
  text-align: center;
}
.nav__item {
  display: inline-block;
}
.nav__link {
  display: inline-block;
  margin-top: 10px;
  padding: 10px 5px;
  font-size: 12px;
  font-weight: 100;
  text-transform: uppercase;
  color: #000;
  box-shadow: 2px 2px 3px #e8e8e8;
}
.nav__link:hover {
  background: #8bc427;
  color: #fff;
  box-shadow: 2px 2px 3px #8bc427;
}
.nav__item:nth-child(2) {
  margin-left: 30px;
}
.nav__item:nth-child(3) {
  margin-left: 26px;
}
.nav__item:nth-child(4) {
  margin-left: 12px;
}
.nav__item:nth-child(6) {
  margin-left: 11px;
}
.nav__item:nth-child(7) {
  margin-left: 14px;
}
.nav__item:nth-child(8) {
  margin-left: 11px;
}
.nav__item:nth-child(9) {
  margin-left: 11px;
}
.footer {
  position: relative;
  background: #484848;
  width: 980px;
  margin: 0 auto;
}
.footer:before {
  content: '';
  position: absolute;
  display: block;
  left: 60px;
  top: -192px;
  width: 880px;
  height: 251px;
  background: url("../img/footer-books-pic.png");
}
.footer__top {
  width: 880px;
  padding-bottom: 20px;
  margin: 0 auto;
  font-size: 0;
  border-bottom: 1px solid #868686;
}
.footer__column {
  display: inline-block;
  vertical-align: bottom;
}
.footer__column_6 {
  width: 190px;
}
.footer__column_5 {
  width: 180px;
}
.footer__column_4 {
  width: 155px;
}
.footer__column_3 {
  width: 120px;
}
.footer__column_2 {
  width: 125px;
}
.footer__column_1 {
  width: 110px;
}
.footer__nav_2 {
  padding-left: 5px;
}
.footer__nav_3 {
  padding-left: 10px;
}
.footer__nav_4 {
  padding-left: 10px;
}
.footer__nav-title {
  padding-bottom: 15px;
  padding-top: 30px;
  font-size: 12px;
  line-height: 14px;
  font-weight: 100;
  color: #fff;
}
.footer__nav-link {
  font-size: 12px;
  line-height: 21px;
  color: #b2b2b2;
}
.footer__contacts {
  font-size: 12px;
  padding-left: 35px;
}
.footer__contacts-title {
  margin-bottom: 10px;
  font-weight: 100;
  line-height: 14px;
  color: #fff;
}
.footer__contacts-info {
  position: relative;
  display: block;
  margin-bottom: 15px;
  color: #898989;
  font-size: 11px;
  font-weight: 300;
}
.footer__contacts-info:nth-child(2) {
  margin-bottom: 5px;
}
.footer__contacts-info:nth-child(5) {
  margin-bottom: 5px;
}
.footer__contacts-text-color {
  color: #fff;
  padding-right: 2px;
}
.footer__contacts-info:before {
  content: '';
  position: absolute;
  display: block;
  left: -30px;
  top: 5px;
  width: 20px;
  height: 20px;
  background: url("../img/phone-pic.png") no-repeat;
}
.footer__contacts-info_mail:before {
  top: 0;
  background: url("../img/mail-pic.png") no-repeat;
}
.footer__contacts-info_skype:before {
  top: -4px;
  background: url("../img/skype-pic.png") no-repeat;
}
.footer__contacts-info_tel:before {
  background: none;
}
.footer__social {
  text-align: right;
}
.footer__social-link {
  display: inline-block;
  width: 28px;
  height: 27px;
  margin-left: 5px;
  margin-bottom: 10px;
  background: url("../img/spritesheet.png") -140px -94px;
}
.footer__social-link_tw {
  background: url("../img/spritesheet.png") -90px -102px;
}
.footer__social-link_fb {
  background: url("../img/spritesheet.png") -8px -61px;
}
.footer__social-link:hover {
  background: url("../img/spritesheet.png") -209px -8px;
}
.footer__social-link_tw:hover {
  background: url("../img/spritesheet.png") -165px -50px;
}
.footer__social-link_fb:hover {
  background: url("../img/spritesheet.png") -96px -61px;
}
.footer__social-link:active {
  background: url("../img/spritesheet.png") -134px -136px;
}
.footer__social-link_tw:active {
  background: url("../img/spritesheet.png") -165px -5px;
}
.footer__social-link_fb:active {
  background: url("../img/spritesheet.png") -52px -59px;
}
.footer__bottom {
  width: 880px;
  margin: 0 auto;
  padding-top: 30px;
  padding-bottom: 30px;
  text-align: center;
}
.footer__copyright {
  display: block;
  font-size: 10px;
  font-weight: 100;
  line-height: 14px;
  color: #999;
}
.slider-box {
  background: #f1f1f1;
}
.slider__pic {
  position: relative;
  display: inline-block;
  margin-top: 17px;
  margin-bottom: 18px;
  margin-left: 20px;
  vertical-align: top;
}
.slider__text {
  position: absolute;
  top: 0;
  left: 0;
}
.slider__title {
  display: block;
  margin-top: 18px;
  margin-left: 25px;
  font-size: 18px;
  font-weight: 100;
  line-height: 21px;
  color: #fff;
}
.slider__subtitle {
  display: block;
  width: 223px;
  margin-top: 115px;
  margin-left: 25px;
  font-size: 14px;
  line-height: 18px;
  color: #d7f792;
}
.slider__exp {
  display: block;
  margin-left: 25px;
  font-size: 11px;
  font-weight: 100;
  font-style: italic;
  line-height: 21px;
  color: #d7f792;
}
.slider__list {
  display: inline-block;
  margin-top: 30px;
  margin-left: 35px;
}
.slider__item {
  position: relative;
}
.slider__link {
  font-size: 14px;
  line-height: 28px;
  font-weight: 100;
  color: #000;
}
.slider__link:hover {
  color: #b3d677;
}
.slider__item:before {
  content: '';
  position: absolute;
  display: block;
  left: 0px;
  bottom: 0px;
  width: 127px;
  height: 1px;
  background: url("../img/img-dot-line.png");
}
.slider__item:last-child:before {
  display: none;
}
.s-guide-box {
  padding-top: 25px;
  padding-bottom: 35px;
}
.guide-box {
  display: inline-block;
  margin-left: 21px;
}
.guide-box:nth-child(2) {
  margin-left: 190px;
}
.guide-box:nth-child(3) {
  margin-left: 150px;
}
.guide__title {
  padding-bottom: 10px;
  font-size: 26px;
  font-weight: 700;
  line-height: 32px;
  text-transform: uppercase;
  color: #3c3c3b;
}
.guide__item {
  position: relative;
  padding-left: 15px;
}
.guide__link {
  font-size: 14px;
  font-weight: 100;
  line-height: 21px;
  color: #3c3c3b;
}
.guide__link:hover {
  color: #8ac427;
}
.guide__item:hover:before {
  background: #8ac427;
}
.guide__item:before {
  content: '';
  position: absolute;
  display: block;
  left: 0;
  top: 7px;
  width: 6px;
  height: 7px;
  background: #3b3b3a;
  border-radius: 50%;
}
.s-guide-box__link {
  display: block;
  margin-top: 15px;
  padding-right: 20px;
  font-size: 14px;
  font-weight: 100;
  color: #999;
  text-align: right;
}
.s-advice-box {
  padding-bottom: 40px;
}
.s-advice-box__title {
  font-size: 26px;
  font-weight: 700;
  line-height: 32px;
  text-transform: uppercase;
  color: #3c3c3b;
}
.s-advice-box__text {
  display: block;
  margin-top: 10px;
  font-size: 14px;
  font-style: italic;
  line-height: 14px;
  color: #575756;
}
.advice-box {
  position: relative;
  display: inline-block;
  width: 240px;
  margin-top: 35px;
  margin-left: 100px;
  padding-left: 24px;
  padding-top: 31px;
  padding-bottom: 29px;
  border: 1px solid #9a9a9a;
  box-shadow: 1px 0px 0px #c8c8c8;
}
.advice-box:nth-child(4) {
  margin-left: 120px;
}
.advice-box:nth-child(3) {
  margin-left: 0px;
}
.advice-box__author {
  display: block;
  width: 135px;
  font-size: 14px;
  font-weight: 700;
  line-height: 21px;
  color: #8bc427;
}
.advice-box__job {
  display: block;
  width: 135px;
  margin-top: 5px;
  font-size: 12px;
  font-weight: 100;
  line-height: 14px;
}
.advice-box__question {
  display: block;
  width: 190px;
  margin-top: 20px;
  font-size: 10px;
  line-height: 14px;
  font-weight: 700;
}
.advice-box__answer {
  width: 190px;
  margin-top: 15px;
  font-size: 10px;
  line-height: 14px;
  font-weight: 100;
}
.advice-box:after {
  content: '';
  position: absolute;
  display: block;
  right: -20px;
  top: -5px;
  width: 80px;
  height: 79px;
  background: #000 url("../img/quotes.png");
}
.s-advice-box__btn {
  text-align: center;
}
.s-advice-box__link {
  display: inline-block;
  margin-top: 20px;
  padding: 7px 25px;
  font-size: 12px;
  font-weight: 100;
  text-transform: uppercase;
  color: #fff;
  background: #8ac427;
  box-shadow: 0px 3px 0px #698b1e;
}
.s-advice-box__link:hover {
  background: #a3dc2e;
}
.s-advice-box__link:active {
  position: relative;
  top: 3px;
  box-shadow: none;
}
.s-update-box {
  padding-bottom: 280px;
}
.s-update-box__title {
  font-size: 26px;
  font-weight: 700;
  line-height: 32px;
  text-transform: uppercase;
}
.questions-answers {
  display: inline-block;
  width: 145px;
  margin-left: 125px;
  margin-top: 40px;
}
.questions-answers__title {
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
}
.questions-answers__text {
  font-size: 14px;
  font-weight: 100;
}
.questions-answers:nth-child(2) {
  margin-left: 0px;
}
.questions-answers:nth-child(6) {
  margin-left: 0px;
}
.b-example {
  background: #f00;
}
